.form__fields {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}




//  (Form)
.form__confirmbtn-wrap {
  margin-top: rem(40);
  width: fit-content;
  margin-inline: auto;

  @include mq(md) {
    margin-top: rem(30);
  }
}

.form__confirmbtn {
  min-width: rem(315);

  @include mq(md) {
    min-width: rem(250);
  }
}



//  ()
.form__confirm-text {
  margin-top: rem(25);
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.875;
  color: #40444a;

  @include mq(md) {
    margin-top: rem(15);
    font-size: rem(13);
    line-height: 1.6;
  }
}

.form__confirm-text-alert {  // 
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.875;
  color: #e04c5f;

  @include mq(md) {
    font-size: rem(13);
    line-height: 1.6;
  }
}

.form__confirm-buttons {
  margin-top: rem(40);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(40);

  @include mq(md) {
    margin-top: rem(20);
    flex-direction: column;
    align-items: center;
    gap: rem(15);
  }
}

//  ()
.form__returnbtn {
  min-width: rem(315);
  width: fit-content;

  @include mq(md) {
    min-width: rem(250);
  }
}

//  ()
.form__sendbtn {
  min-width: rem(315);
  width: fit-content;

  @include mq(md) {
    min-width: rem(250);
  }
}









